
"use client";

import { useState, useEffect } from 'react';

interface CountdownTimerProps {
  targetDate: string;
}

interface TimeLeft {
  days?: number;
  hours?: number;
  minutes?: number;
  seconds?: number;
}

const CountdownTimer: React.FC<CountdownTimerProps> = ({ targetDate }) => {
  const [timeLeft, setTimeLeft] = useState<TimeLeft>({});
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  useEffect(() => {
    if (!isClient) return;

    const calculateTimeLeft = () => {
      const difference = +new Date(targetDate) - +new Date();
      let newTimeLeft: TimeLeft = {};

      if (difference > 0) {
        newTimeLeft = {
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
          minutes: Math.floor((difference / 1000 / 60) % 60),
          seconds: Math.floor((difference / 1000) % 60),
        };
      } else {
        newTimeLeft = { days: 0, hours: 0, minutes: 0, seconds: 0 };
      }
      return newTimeLeft;
    };
    
    setTimeLeft(calculateTimeLeft()); // Initial calculation

    const timer = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);

    return () => clearInterval(timer);
  }, [targetDate, isClient]);

  if (!isClient) {
    // Render placeholder or skeleton on server/initial client render
    return (
      <div className="grid grid-cols-4 gap-4 text-center animate-in fade-in duration-1000 delay-500">
        {[...Array(4)].map((_, i) => (
          <div key={i} className="p-4 rounded-lg">
            <div className="text-3xl sm:text-4xl font-bold text-primary animate-pulse">--</div>
            <div className="text-xs sm:text-sm text-primary uppercase tracking-wider">Loading...</div>
          </div>
        ))}
      </div>
    );
 }
  
  const timerComponents = [
    { label: 'Dias', value: timeLeft.days },
    { label: 'Horas', value: timeLeft.hours },
    { label: 'min', value: timeLeft.minutes },
    { label: 'seg', value: timeLeft.seconds },
  ];

return (
  <div className="grid grid-cols-4 gap-4 text-center w-full max-w-md animate-in fade-in duration-1000 delay-500 mt-9 mb-8">
    {timerComponents.map((component, index) => (
      component.value !== undefined && (
        <div
          key={index}
          className="p-3 sm:p-4 rounded-lg relative group"
        >
          {/* Golden ring effect */}
          <div className="absolute inset-0 rounded-lg border border-white/40 group-hover:border-white/80 transition-all duration-500 shadow-[0_0_15px_rgba(255,255,255,0.3)]"></div>
          
          <div className="relative text-4xl sm:text-5xl font-bold text-[#b58e45] drop-shadow-[0_0_6px_rgba(181,142,69,0.6)] animate-pulse-slow">
            {String(component.value).padStart(2, '0')}
          </div>
          
          <div className="relative text-xs sm:text-sm uppercase tracking-wider text-white font-light mt-1">
            {component.label}
          </div>
        </div>
      )
    ))}
  </div>
);



};

export default CountdownTimer;

    
